import React from "react"
import { StyledApp } from "./App.styled.js"
import { Helmet } from "react-helmet"
import { bindActionCreators } from "redux"
import * as output_actions from "../redux/actions/output"
import * as input_actions from "../redux/actions/input"
import * as ui_actions from "../redux/actions/ui"
import { connect } from "react-redux"
import { object_from_querystring } from "universal-common-scripts/functions/urls"
import { Link } from "gatsby"

const isPhone = function () {
  if (typeof navigator !== "object") {
    return false
  }
  const toMatch = [/Android/i, /webOS/i, /iPhone/i, /iPod/i, /BlackBerry/i, /Windows Phone/i]
  return toMatch.some((toMatchItem) => {
    return navigator.userAgent.match(toMatchItem)
  })
}

const DEBUG1 = true
class App extends React.Component {
  componentDidMount() {
    let url_obj = object_from_querystring(this.props.location.search)
    let tld = url_obj.tld || ""
    if (DEBUG1) console.log("App.js componentDidMount tld", tld)
    this.props.output_actions.RX__tld_add(tld, "user")
    setTimeout(function () {
      window.scrollTo(0, 1)
    }, 100)
  }
  // componentDidUpdate(prevProps) {
  //   // reset inputs if visit homepage
  //   if (!this.props.location.search && prevProps.location.search !== this.props.location.search) {
  //     this.props.input_actions.RX__clear_inputs()
  //   }
  // }
  render() {
    const { children } = this.props
    return (
      <StyledApp>
        <Helmet>
          <meta charSet="utf-8" />
          <title>{this.props.redux__meta_title || this.props.meta_title}</title>
          {/*{!!isPhone() ? (*/}
          {/*  <meta name="viewport" content="width=450, initial-scale=1.0, maximum-scale=1.0" />*/}
          {/*) : (*/}
          {/*  <meta name="viewport" content="width=device-width" />*/}
          {/*)}*/}
          <meta name="viewport" content="width=450, initial-scale=1.0, maximum-scale=1.0, user-scalable=no" />
        </Helmet>
        {children}
        <div className="footerCopyright hide-small">
          <p className="content">
            Built with &nbsp; by <Link to="/about">Paul + Samira</Link>. All rights reserved. By using this site, you
            agree not to programmatically collect our content. Please do not scape our site. Thanks a lot for visiting!
          </p>
        </div>
      </StyledApp>
    )
  }
}

/*******************************************************************************************************
 * this.props DOCUMENTATION
 *******************************************************************************************************/

const mapDispatchToProps = (dispatch) => {
  return {
    output_actions: bindActionCreators(output_actions, dispatch),
    input_actions: bindActionCreators(input_actions, dispatch),
    ui_actions: bindActionCreators(ui_actions, dispatch)
  }
}

const mapStateToProps = function (state) {
  return {
    redux__meta_title: state.ui.meta_title, // string
    input_str: state.input.input_str, // string
    input_tld: state.input.input_tld // string
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App)
